      // If editing existing record
      if (existing._id) {
        editingId.value = existing._id;
      }
    } else {
      currentBalance.value = 0;
      editingId.value = '';
    }
  } catch (err) {
    console.error('Error loading current balance:', err);
    currentBalance.value = null;
  }
};

// Watch for employee and year changes
watch(
